.trick {
  padding-bottom: 50px;
}
.trick__list {
  padding: 0;
  margin: 0;
  list-style: none;
  font-size: 15px;
  line-height: 22px;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
}
.trick__item {
  width: 31.4%;
  flex-shrink: 0;
  border: 1px solid #D5D8DC;
  padding: 25px;
  margin-right: 25px;
}
.trick__item:nth-child(3n) {
  margin-right: 0;
}
.trick__persent {
  width: 93px;
  height: 93px;
  margin: 0 auto;
  margin-bottom: 40px;
  border-radius: 50%;
  background: #FFCD00;
  text-align: center;
  font-size: 32px;
  font-weight: 600;
  line-height: 2.8;
}

@media (max-width: 1024px) {
  .trick__item {
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  .trick__item {
    width: 100%;
    margin-right: 0;
    margin-bottom: 25px;
  }
}